import * as React from "react";
import {useEffect, useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss';
import begin from './img/begin.jpg'
import mid from './img/mid.jpg'
import flor from './img/flor.jpg'
import {Card, Container, Nav, Navbar} from "react-bootstrap";
import {Fade} from "react-reveal";
import {motion, useMotionValue, useSpring, useTransform, useViewportScroll} from "framer-motion"
import {Parallax} from "react-parallax";
import books from './img/books.svg'
import cafe from './img/cafe.svg'
import hacker from './img/hacker.svg'
import TodoList from "./components/TodoList";
import todolist from './img/todolist.svg'


const App = () => {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const x = useMotionValue(0);
    const xInput = [-100, 0, 100];
    const tickPath = useTransform(x, [10, 100], [0, 1]);
    const crossPathA = useTransform(x, [-10, -55], [0, 1]);
    const crossPathB = useTransform(x, [-50, -100], [0, 1]);


    const [isComplete, setIsComplete] = useState(false);
    const [pop, setPop] = useState(false)
    const {scrollYProgress} = useViewportScroll();
    const yRange = useTransform(scrollYProgress, [0, 0.9], [0, 1]);
    const pathLength = useSpring(yRange, {stiffness: 400, damping: 90});

    useEffect(() => yRange.onChange(v => setIsComplete(v >= 1)), [yRange]);

    return (
        <div>
            <Navbar bg="dark" variant="dark" fixed='top'>
                <Container className='justify-content-center'>
                    <svg className="progress-icon" viewBox="0 0 60 60">
                        <motion.path
                            fill="none"
                            strokeWidth="5"
                            stroke="white"
                            strokeDasharray="0 1"
                            d="M 0, 20 a 20, 20 0 1,0 40,0 a 20, 20 0 1,0 -40,0"
                            style={{
                                pathLength,
                                rotate: 90,
                                translateX: 5,
                                translateY: 5,
                                scaleX: -1 // Reverse direction of line animation
                            }}
                        />
                        <motion.path
                            fill="none"
                            strokeWidth="5"
                            stroke="white"
                            d="M14,26 L 22,33 L 35,16"
                            initial={false}
                            strokeDasharray="0 1"
                            animate={{pathLength: isComplete ? 1 : 0}}
                        />
                    </svg>
                    <Nav>
                        <motion.div
                            whileHover={{scale: 1.2}}
                            whileTap={{
                                scale: 0.9,
                                borderRadius: "100%"
                            }}><Nav.Link href="#home">Home</Nav.Link>
                        </motion.div>
                        <motion.div
                            whileHover={{scale: 1.2}}
                            whileTap={{
                                scale: 0.9,
                                borderRadius: "100%"
                            }}><Nav.Link href="#home">Home</Nav.Link>
                        </motion.div>
                        <motion.div
                            whileHover={{scale: 1.2}}
                            whileTap={{
                                scale: 0.9,
                                borderRadius: "100%"
                            }}><Nav.Link href="#home">Home</Nav.Link>
                        </motion.div>
                    </Nav>
                </Container>
            </Navbar>

            <Fade left>

                <div>
                    <Card className="card-a">
                        <motion.div initial={{opacity: 0.5}}
                                    animate={{opacity: 1}}
                                    transition={{duration: 5}}>
                            <Parallax bgImage={mid} strength={200} style={{overflow: 'visible', position: 'relative'}}>
                                <div style={{height: '100vh'}}>
                                </div>
                            </Parallax>
                        </motion.div>

                        <Card.ImgOverlay className='first-plan' style={{color: 'white'}}>
                            <Card.Title className='text-center'>
                                <motion.div>
                                    <motion.p initial={{opacity: 1, y: -400}}
                                              animate={{opacity: 0, y: 300, scale: 2, x: -100}}
                                              transition={{type: "spring", duration: 7}}
                                    >BE READY
                                    </motion.p>
                                    <motion.p
                                        initial={{opacity: 1, y: -400}}
                                        animate={{opacity: 0, y: 300, scale: 2, x: 100}}
                                        transition={{type: "spring", delay: 2, duration: 7}}
                                    >TO HAVE LITTLE BIT
                                    </motion.p>
                                    <motion.h1
                                        initial={{opacity: 1, y: -400}}
                                        animate={{opacity: 0, y: 300, scale: 2}}
                                        transition={{type: "spring", delay: 4, duration: 9}}
                                    >MAGIC
                                    </motion.h1>
                                </motion.div>
                            </Card.Title>
                            <Card.Text className='text-center'>
                                <motion.h1
                                    initial={{opacity: 0, scale: 4}}
                                    animate={{opacity: 1, scale: 1}}
                                    transition={{type: "spring", duration: 2, delay: 6}}>
                                    PORTFOLIO
                                </motion.h1>
                                <motion.h2
                                    initial={{opacity: 0, scale: 3}}
                                    animate={{opacity: 1, scale: 1}}
                                    transition={{type: "spring", duration: 2, delay: 7}}>
                                    MAGOMEDOV SHAMIL ARSENOVICH
                                </motion.h2>
                            </Card.Text>
                        </Card.ImgOverlay>
                    </Card>
                </div>
                <div>
                    <Card className="card-a">
                        <Parallax bgImage={begin} strength={200} style={{overflow: 'visible', position: 'relative'}}>
                            <div style={{height: '100vh'}}>
                            </div>
                        </Parallax>

                        <Card.ImgOverlay className='second-plan'>
                            <Card.Title>My last works</Card.Title>
                            <motion.ul
                                        className='surface'>

                                <motion.li className='box'
                                           style={{ x }}
                                           drag="x"
                                           dragConstraints={{ left: 0, right: 0 }}
                                            whileHover={{rotate: 10, opacity: 0.5}}
                                            whileTap={{rotate: -180, borderRadius: '100%'}}
                                           onClick={() => window.open('https://kulinec05.github.io/avito/')}
                                >
                                    <img src={hacker}/>
                                </motion.li>
                                <motion.li className='box'
                                            whileHover={{rotate: 10, opacity: 0.5}}
                                            whileTap={{rotate: -180, borderRadius: '100%'}}
                                           onClick={() => window.open('https://kulinec05.github.io/Cafe/')}>
                                    <img src={cafe}/>
                                </motion.li>


                                <motion.li className='box'
                                            whileHover={{rotate: 10, opacity: 0.5}}
                                            whileTap={{rotate: -180, borderRadius: '100%'}}
                                           onClick={() => window.open('https://kulinec05.github.io/bookSearch/')}>
                                    <img src={books}/>
                                </motion.li>
                                <motion.li className='box'
                                            whileHover={{rotate: 10, opacity: 0.5}}
                                           onClick={handleShow}  whileTap={{rotate: -180, borderRadius: '100%'}}>

                                    <img src={todolist}/>
                                </motion.li>


                            </motion.ul>
                            <Card.Text>Last updated 3 mins ago</Card.Text>
                        </Card.ImgOverlay>
                    </Card>
                </div>
                <div>
                    <Card className="card-a">

                        <Parallax bgImage={flor} strength={200} style={{overflow: 'visible', position: 'relative'}}
                                  renderLayer={(percentage) => (
                                      !pop ? <div>
                                          <div
                                              onClick={() => setPop(true)}
                                              style={{
                                                  position: "absolute",
                                                  background: `rgba(255, 125, 0, ${percentage * 1})`,
                                                  left: "50%",
                                                  top: "50%",
                                                  borderRadius: "50%",
                                                  transform: "translate(-50%,-50%)",
                                                  width: percentage * 500,
                                                  height: percentage * 500,
                                                  color: 'black'
                                              }}
                                          />
                                      </div> : null
                                  )}>
                            <div style={{height: '100vh'}}>
                            </div>
                        </Parallax>


                        <Card.ImgOverlay className='second-plan'>
                            <TodoList show={show}
                                      handleClose={handleClose}
                            />
                        </Card.ImgOverlay>
                    </Card>
                </div>
            </Fade>

        </div>
    );
}

export default App;
